import React from 'react';
import { Row, Col } from 'react-bootstrap';
import ProductMonth from './charts/product_month';
import ProductSeries from './charts/product_series';
import ProductMonthYearSelect from '../utils/select/product_month_select';
import DepotPieChart from './charts/depot_pie_chart';
import DepotMonthSelect from '../utils/select/depot_month_select';

class ChartSection extends React.Component {
    render() {

        return (
            <div className='charts-section'>
                <Row >
                    <Col>
                        <div className='chart-area'>
                            <ProductSeries
                                product_series={this.props.product_series}
                                selection_dates={this.props.selection_dates}
                                dashboard_metric={this.props.dashboard_metric}
                                currency_type={this.props.currency_type}
                                update_product_series={this.props.update_product_series}
                            />
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col xs={7}>
                        <div className='chart-area'>
                            <ProductMonthYearSelect />
                            <ProductMonth
                                product_month={this.props.product_month}
                                dashboard_metric={this.props.dashboard_metric}
                                currency_type={this.props.currency_type}
                                product_month_year={this.props.product_month_year}
                                update_product_month={this.props.update_product_month}
                            />
                        </div>
                    </Col>
                    <Col xs={5}>
                        <div className='chart-area'>
                            <DepotMonthSelect />
                            <DepotPieChart
                                depot_month={this.props.depot_month}
                                dashboard_metric={this.props.dashboard_metric}
                                depot_month_month={this.props.depot_month_month}
                                depot_month_year={this.props.depot_month_year}
                                currency_type={this.props.currency_type}
                                update_depot_month={this.props.update_depot_month}
                            />
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default ChartSection;