import React from 'react'
import ReactApexChart from 'react-apexcharts'
import ApexCharts from 'apexcharts';

import { currency_type_display, truncate } from '../../utils/_funcs';
import SpinnerComponent from '../../utils/spinner';

const mapData = (data, currency_type) => {

    let values = []
    let maxs = []

    for (let i = 0; i < data.length; i++) {


        values.push([data[i].timestamp, currency_type === "Million" ? truncate(data[i].sum / 1000000) : truncate(data[i].sum / 1000)])
    }

    return values
}



const update = (props, currency_type) => {
    let data = props.product_series
    let series = []
    for (let i = 0; i < data.length; i++) {
        let prod = props.dashboard_metric ? data[i].revenue : data[i].quantity
        let d = mapData(prod, currency_type)
        series = series.concat({
            name: data[i].name,
            data: d
        })
    }

    return series
}
class ProductSeries extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currency_type: this.props.currency_type,
            selection_dates: this.props.selection_dates,
            product_series: this.props.product_series,
            spinner: false,
            series: [],
            options: {
                chart: {
                    id: 'area-datetime',
                    type: 'area',
                    height: 400,
                    zoom: {
                        autoScaleYaxis: false
                    }
                },
                title: {
                    text: this.props.dashboard_metric ? "Revenue" : "Quantities"
                },
                annotations: {

                    xaxis: [{
                        x: new Date(`${this.props.selection_dates.startDate}`).getTime(),
                        borderColor: '#999',
                        yAxisIndex: 0,

                    }]
                },
                dataLabels: {
                    enabled: true
                },
                markers: {
                    size: 0,
                    style: 'hollow',
                },
                xaxis: {
                    type: 'datetime',
                    // min: new Date(`${this.props.selection_dates.startDate}`).getTime(),
                    // max: new Date(`${this.props.selection_dates.endDate}`).getTime(),
                    tickAmount: 5,
                },
                yaxis: {
                    title: {
                        text: this.props.dashboard_metric ? `Kshs (${this.props.currency_type})` : `Litres (${this.props.currency_type})`
                    },
                    min: 0,

                },
                tooltip: {
                    y: {
                        formatter: this.props.dashboard_metric ? function (val) {
                            return `Kshs ${val} Millions`
                        } : function (val) {
                            return `${val} Litres`
                        }
                    }
                },

            },
            selection: 'one_month',

        };
    }

    static getDerivedStateFromProps = async (props, state) => {

        const { dashboard_metric, selection_dates, currency_type, update_product_series } = props

        if (update_product_series === true) {
            state.spinner = true
            return state
        }

        state.spinner = false

        if (typeof (selection_dates.startDate) === "string") {
            selection_dates.startDate = new Date(`${selection_dates.startDate}`)
            selection_dates.endDate = new Date(`${selection_dates.endDate}`)
        }
        let data = update(props, currency_type)
        state.currency_type = props.currency_type
        state.series = data
        state.options.title.text = dashboard_metric ? "Revenue" : "Quantities"
        // state.options.xaxis.min = selection_dates.startDate.getTime()
        // state.options.xaxis.max = selection_dates.endDate.getTime()
        // state.options.yaxis.max = data[1] + 2
        let c = currency_type
        state.options.yaxis.title.text = dashboard_metric ? `Kshs (${c})` : `Litres (${c})`

        state.options.tooltip.y.formatter = dashboard_metric ? function (val) {
            return `Kshs ${val}${currency_type_display(currency_type)}`
        } : function (val) {
            return `${val}${currency_type_display(currency_type)} Litres`
        }

        state.selection_dates = selection_dates
        state.product_series = props.product_series
        return state
    }

    render() {
        try {

            ApexCharts.exec(
                'area-datetime',
                'zoomX',
                new Date(`${this.state.selection_dates.startDate}`).getTime(),
                new Date(`${this.state.selection_dates.endDate}`).getTime(),

            )
        } catch (e) {
            "in error"
        }
        return (
            <div id="chart-timeline">
                {this.state.spinner ? <div style={{ height: 400, position: "relative" }}> <SpinnerComponent spinner={this.state.spinner} /></div> :
                    <ReactApexChart options={this.state.options} series={this.state.series} type="area" height={400} />}
            </div>
        );
    }
}

export default ProductSeries