import React from "react";
import { Form } from "react-bootstrap"
import { connect } from "react-redux"
import { UPDATE_DEPOT_MONTH_MONTH_ACTION, UPDATE_DEPOT_MONTH_YEAR_ACTION, GET_DEPOT_MONTH_ACTION, UPDATE_DEPOT_MONTH_ACTION } from "../../../redux/actions/actions"


class DepotMonthSelect extends React.Component {
    state = {
        year: this.props.depot_month_year,
        month: this.props.depot_month_month,
    }

    static getDerivedStateFromProps = (props, state) => {
        if (state.year !== props.depot_month_year || state.month !== props.depot_month_month) {
            state.year = props.depot_month_year
            state.month = props.depot_month_month
            return state
        }
        return null

    }

    handleYearChange = async (year) => {
        await this.props.UPDATE_DEPOT_MONTH_ACTION()
        await this.props.UPDATE_DEPOT_MONTH_YEAR_ACTION(year)
        await this.props.GET_DEPOT_MONTH_ACTION(year)
    }

    handleMonthChange = async (month) => {
        await this.props.UPDATE_DEPOT_MONTH_MONTH_ACTION(month)
    }

    render() {
        const years = [2022, 2021]
        const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
        return (
            <div className="pie-select">
                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Year</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleYearChange(e.target.value)}>
                        <option>{this.state.year}</option>
                        {years.map((year, idx) =>
                            parseInt(this.state.year) === year ? null : <option key={idx} value={year}>{year}</option>
                        )}

                    </Form.Select>
                </Form.Group>

                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Month</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleMonthChange(e.target.value)}>
                        {parseInt(this.state.month) === 13 ? null : <option value={this.state.month}>{months[parseInt(this.state.month)]}</option>}
                        <option value={13}>All</option>
                        {months.map((month, idx) =>
                            parseInt(this.state.month) === idx ? null : <option key={idx} value={idx}>{month}</option>
                        )}

                    </Form.Select>
                </Form.Group>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        depot_month_month: state.update_reducer.depot_month_month,
        depot_month_year: state.update_reducer.depot_month_year,
    };
};

const mapDispatchToProps = {
    UPDATE_DEPOT_MONTH_MONTH_ACTION,
    UPDATE_DEPOT_MONTH_YEAR_ACTION,
    GET_DEPOT_MONTH_ACTION,
    UPDATE_DEPOT_MONTH_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DepotMonthSelect);
