import React from "react";
import { Form } from "react-bootstrap"
import { connect } from "react-redux"
import { UPDATE_PRODUCT_MONTH_YEAR_ACTION, GET_PRODUCT_MONTH_ACTION, UPDATE_PRODUCT_MONTH_ACTION } from "../../../redux/actions/actions"

class ProductMonthYearSelect extends React.Component {
    state = {
        year: this.props.product_month_year
    }

    static getDerivedStateFromProps = (props, state) => {
        state.year = props.product_month_year
        return state
    }

    handleYearChange = async (year) => {
        await this.props.UPDATE_PRODUCT_MONTH_ACTION()
        await this.props.UPDATE_PRODUCT_MONTH_YEAR_ACTION(year)
        await this.props.GET_PRODUCT_MONTH_ACTION(year)
    }
    render() {
        const years = [2022, 2021]

        return (
            <div>
                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Year</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleYearChange(e.target.value)}>
                        <option>{this.state.year}</option>
                        {years.map((year, idx) =>
                            parseInt(this.state.year) === year ? null : <option key={idx} value={year}>{year}</option>
                        )}

                    </Form.Select>
                </Form.Group>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        product_month_year: state.update_reducer.product_month_year,
    };
};

const mapDispatchToProps = {
    UPDATE_PRODUCT_MONTH_YEAR_ACTION,
    GET_PRODUCT_MONTH_ACTION,
    UPDATE_PRODUCT_MONTH_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ProductMonthYearSelect);
