import React from "react"
import ReactApexChart from 'react-apexcharts'
import { MONTHS } from "../../../utils/_consts"
import SpinnerComponent from "../../utils/spinner"
import { currency_type_display, truncate } from "../../utils/_funcs"

const mapData = (data, currency_type) => {

    let months = new Array(12).fill(0)
    for (const month in data) {
        let month_value = data[month]
        months[month_value.date__month - 1] = currency_type === "Million" ? truncate(month_value.sum / 1000000) : truncate(month_value.sum / 1000)
    }
    return months
}

class ProductMonth extends React.Component {
    constructor(props) {
        super(props);
        const year = new Date().getFullYear();
        this.state = {
            spinner: false,
            series: [],
            options: {
                chart: {
                    type: 'bar',
                    height: 350
                },
                plotOptions: {
                    bar: {
                        horizontal: false,
                        columnWidth: '55%',
                        endingShape: 'rounded'
                    },
                },
                dataLabels: {
                    enabled: false
                },
                title: {
                    text: this.props.dashboard_metric ? `${year} Products Trends by Month's Revenue` : `${year} Products Trends by Month's Quantity`,
                    align: 'left'
                },
                stroke: {
                    show: true,
                    width: 2,
                    colors: ['transparent']
                },
                xaxis: {
                    categories: MONTHS,
                },
                yaxis: {
                    title: {
                        text: this.props.dashboard_metric ? "Kshs (Millions)" : "Litres"
                    }
                },
                fill: {
                    opacity: 1
                },
                tooltip: {
                    y: {
                        formatter: this.props.dashboard_metric ? function (val) {
                            return `Kshs ${val} Millions`
                        } : function (val) {
                            return `${val} Litres`
                        }
                    }
                }
            },


        };
    }

    static getDerivedStateFromProps = (props, state) => {
        const products = props.product_month
        const year = props.product_month_year
        if (props.update_product_month === true) {
            state.spinner = true
            return state
        } else {
            state.spinner = false
        }
        let series = []

        for (let i = 0; i < products.length; i++) {
            let product = products[i]
            series = series.concat(
                {
                    name: product.name,
                    data: props.dashboard_metric ? mapData(product.revenue, props.currency_type) : mapData(product.quantity, props.currency_type)
                }
            )
        }

        state.series = series
        state.options.title.text = props.dashboard_metric ? `${year} Products Trends by Month's Revenue` : `${year} Products Trends by Month's Quantity`
        state.options.yaxis.title.text = props.dashboard_metric ? `Kshs (${props.currency_type})` : `Litres (${props.currency_type})`
        state.options.tooltip.y.formatter = props.dashboard_metric ? function (val) {
            return `Kshs ${val}${currency_type_display(props.currency_type)}`
        } : function (val) {
            return `${val}${currency_type_display(props.currency_type)} Litres`
        }
        return state
    }

    render() {

        return (
            <div id="chart" style={{ marginTop: 20 }}>
                {this.state.spinner ? <div style={{ height: 350, position: "relative" }}><SpinnerComponent spinner={this.state.spinner} /> </div> :
                    <ReactApexChart options={this.state.options} series={this.state.series} type="bar" height={350} />
                }
            </div>
        );
    }
}
export default ProductMonth;